let storageUtil = {
  set: function (key, value, expiredM) {
    /**
     * set Â∏¶ËøáÊúüÊó∂Èó¥ÁöÑÂ≠òÂÇ®ÊñπÊ≥ï
     * @ param {String}     key ÈîÆ
     * @ param {String}     value ÂÄºÔºå
     * @ param {String}     expired ËøáÊúüÊó∂Èó¥Ôºå‰ª•ÂàÜÈíü‰∏∫Âçï‰Ωç
     */
    let expiredTime = Date.now() + expiredM * 60000
    let item = {
      value: value,
      expired: expiredTime
    }
    let itemStr = JSON.stringify(item)
    localStorage.setItem(key, itemStr)
  },

  get: function (key) {
    /**
     * get Ëé∑ÂèñÊñπÊ≥ï,È°ª‰∏ésetÈÖçÂ•ó‰ΩøÁî®
     * @ param {String}     key ÈîÆ
     */
    let valueStr = localStorage.getItem(key)
    if (valueStr != null) {
      let item = JSON.parse(valueStr)
      if (Date.now() >= item.expired) {
        localStorage.removeItem(key)
        return null
      } else {
        return item.value
      }
    } else {
      return null
    }
  }
};
export default storageUtil;
